.App {
  text-align: center;
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-body {
  background-color: white;
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
}

.App-footer {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
  left: 0;
  bottom: 0;
  height: 100px;
  width: 100%;
  overflow: hidden;
}

.error {
  display: none;
}
.error.active {
  display: block;
  color: red;
}

.fade-out {
  visibility: hidden;
  opacity: 0;
  transition: visibility 1s 0.5s, opacity 0.5s;
}